<template> 
  <div class="app-container">
    <el-card class="filter-container" shadow="never">
      <div>
        <i class="el-icon-search"></i>
        <span>筛选搜索</span>
        <el-button
          style="float: right"
          @click="handleSearchList()"
          type="primary"
          size="small">
          查询结果
        </el-button>
        <el-button
          style="float: right;margin-right: 15px"
          @click="handleResetSearch()"
          size="small">
          重置
        </el-button>
      </div>
      <div style="margin-top: 15px">
        <el-form :inline="true" :model="listQuery" size="small" label-width="140px">
          <el-form-item label="输入搜索：">
            <el-input style="width: 203px" v-model="listQuery.name" placeholder="菜品名称"/>
          </el-form-item>
          <el-form-item label="菜品分类：">
            <el-select v-model="listQuery.categoryId" placeholder="请选择分类" clearable>
              <el-option
                v-for="item in productCateOptions"
                :key="item.value"
                :label="item.label"
                :value="item.value">
              </el-option>
            </el-select>
          </el-form-item>
        </el-form>
      </div>
    </el-card>
    <el-card class="operate-container" shadow="never">
      <i class="el-icon-tickets"/>
      <span>数据列表</span>
      <el-button
        class="btn-add"
        @click="handleAddProduct()"
        size="mini">
        添加
      </el-button>
    </el-card>
    <div class="table-container">
      <el-table ref="productTable"
                :data="list"
                style="width: 100%"
                @selection-change="handleSelectionChange"
                v-loading="listLoading"
                border>
        <el-table-column label="编号" width="100" align="center">
          <template slot-scope="scope">{{scope.row.id}}</template>
        </el-table-column>
        <el-table-column label="菜品名称" align="center">
          <template slot-scope="scope">
            <p>{{scope.row.name}}</p>
          </template>
        </el-table-column>
        <el-table-column label="菜品图片" width="200" align="center">
          <template slot-scope="scope"><img style="height: 80px" :src="scope.row.picture"></template>
        </el-table-column>

        <el-table-column label="菜品类别" width="200" align="center">
          <template slot-scope="scope" v-if="productCateOptions.length>0 && scope.row.categoryId!==null">{{productCateOptions.find(i=>i.value===scope.row.categoryId).label}}</template>
        </el-table-column>
        <el-table-column label="描述" width="250" align="center">
          <template slot-scope="scope">
            <p>{{scope.row.detail}}</p>
          </template>
        </el-table-column>

        <el-table-column label="价格" width="120" align="center">
          <template slot-scope="scope">
            <p>￥{{scope.row.price}}</p>
          </template>
        </el-table-column>
        <el-table-column label="操作" width="160" align="center">
          <template slot-scope="scope">
              <el-button
                size="mini"
                @click="handleUpdateProduct(scope.$index, scope.row)">编辑
              </el-button>
              <el-button
                size="mini"
                type="danger"
                @click="handleDelete(scope.$index, scope.row)">删除
              </el-button>
          </template>
        </el-table-column>
      </el-table>
    </div>
    <div class="batch-operate-container" >

    </div>
    <div class="pagination-container">
      <el-pagination
        background
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        layout="total, sizes,prev, pager, next,jumper"
        :page-size="listQuery.pageSize"
        :page-sizes="[5,10,15]"
        :current-page.sync="listQuery.pageNum"
        :total="total">
      </el-pagination>
    </div>
  </div>
</template>
<script>
  import {
    fetchList,
    updateDeleteStatus,
  } from '@/api/product'
  import {getAllCategoryList} from '@/api/productCate'

  const defaultListQuery = {
    name: null,
    pageNum: 1,
    pageSize: 5,
    categoryId: null,
  };
  export default {
    name: "productList",
    data() {
      return {
        editSkuInfo:{
          dialogVisible:false,
          productId:null,
          productSn:'',
          productAttributeCategoryId:null,
          stockList:[],
          productAttr:[],
          keyword:null
        },
        operateType: null,
        listQuery: Object.assign({}, defaultListQuery),
        list: null,
        total: null,
        listLoading: true,
        selectProductCateValue: null,
        multipleSelection: [],
        productCateOptions: [],
      }
    },
    created() {
      this.getList();
      this.getProductCateList();
    },
    watch: {
      selectProductCateValue: function (newValue) {
          this.listQuery.categoryId = newValue[1];
      }
    },
    methods: {
      getList() {
        this.listLoading = true;
        fetchList(this.listQuery).then(response => {
          this.listLoading = false;
          this.list = response.data.list;
          this.total = response.data.total;
        });
      },
      getProductCateList() {
        getAllCategoryList(1,100).then(response => {
          let list = response.data.list;
          this.productCateOptions = [];
          for (let i = 0; i < list.length; i++) {
            this.productCateOptions.push({label: list[i].name,value:list[i].id})
          }
        });
      },
      handleSearchList() {
        this.listQuery.pageNum = 1;
        this.getList();
      },
      handleAddProduct() {
        this.$router.push({path:'/pms/addProduct'});
      },
      handleSizeChange(val) {
        this.listQuery.pageNum = 1;
        this.listQuery.pageSize = val;
        this.getList();
      },
      handleCurrentChange(val) {
        this.listQuery.pageNum = val;
        this.getList();
      },
      handleSelectionChange(val) {
        this.multipleSelection = val;
      },

      handleResetSearch() {
        this.selectProductCateValue=[];
        this.listQuery = Object.assign({}, defaultListQuery);
      },
      handleDelete(index, row){
        this.$confirm('是否要进行删除操作?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
          let id = [];
          id.push(row.id);
          this.updateDeleteStatus(1,id);
        });
      },
      handleUpdateProduct(index,row){
        this.$router.push({path:'/pms/updateProduct',query:{id:row.id}});
      },
      updateDeleteStatus(deleteStatus, id) {
        let params = new URLSearchParams();
        params.append('id', id);
        updateDeleteStatus(params).then(response => {
          this.$message({
            type: 'success',
            message: '删除成功',
            duration: 2000
          });
        }
        );
        this.$router.back()
      }
    }
  }
</script>
<style></style>





